import React, { useState, useEffect } from 'react';
import './App.css';
import Papa from 'papaparse';
import TextField from '@mui/material/TextField';
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme({
    components: {
        MuiTextField: {
            styleOverrides: {
                root: {
                    '& .MuiInputBase-input': {
                        color: '#E0E0E0', // Text color
                    },
                    '& .MuiInputLabel-root': {
                        color: '#E0E0E0', // Label color
                    },
                    '& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline': {
                        borderColor: '#E0E0E0', // Outline color
                    },
                    '&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline': {
                        borderColor: '#E0E0E0', // Outline color on hover
                    },
                    '& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline': {
                        borderColor: '#E0E0E0', // Outline color when focused
                    },
                },
            },
        },
    },
});

const PitScouting = ({ dataType }) => {
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');
    const [filteredData, setFilteredData] = useState([]);

    const fetchData = async () => {
        setLoading(true);
        const publicSpreadsheetUrl = `https://docs.google.com/spreadsheets/d/e/2PACX-1vRojRhLgZSPXJopPdni1V4Z-inXXY3a__2NaVMsoJHPs9d25ZQ7t56QX67mncr6yo-w4B8WCWyHFe2m/pub?output=csv&gid=1325302827`;
        const cacheBuster = `cacheBuster=${new Date().getTime()}`;
        const urlWithCacheBuster = `${publicSpreadsheetUrl}&${cacheBuster}`;

        try {
            Papa.parse(urlWithCacheBuster, {
                download: true,
                header: true,
                complete: function (results) {
                    setData(results.data);
                    setFilteredData(results.data); // Initialize filtered data with all data
                    setLoading(false);
                },
                error: function (error) {
                    console.warn('Error fetching data from Google Sheets', error);
                    setLoading(false);
                },
            });
        } catch (error) {
            console.error('Fetching data failed', error);
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchData();
    }, []);

    const handleSearchChange = (event) => {
        const query = event.target.value;
        setSearchQuery(query);

        const filtered = data.filter(team =>
            team['Team Number'].toString().toLowerCase().includes(query.toLowerCase())
        );
        setFilteredData(filtered);
    };

    return (
        <ThemeProvider theme={theme}>
            <div className="pit-scouting-container">
                <div className="search-bar">
                    <TextField
                        label="Search Team"
                        variant="outlined"
                        value={searchQuery}
                        onChange={handleSearchChange}
                        fullWidth
                        sx={{
                            input: { color: '#E0E0E0' },
                            label: { color: '#E0E0E0' },
                            '.MuiOutlinedInput-root': {
                                '& fieldset': {
                                    borderColor: '#E0E0E0',
                                },
                                '&:hover fieldset': {
                                    borderColor: '#E0E0E0',
                                },
                                '&.Mui-focused fieldset': {
                                    borderColor: '#E0E0E0',
                                },
                            },
                        }}
                    />
                </div>
                {loading && (
                    <div className="spinner-container">
                        <div className="spinner"></div>
                    </div>
                )}
                {!loading && filteredData.length > 0 && (
                    <div className="data-table">
                        <table>
                            <thead>
                                <tr>
                                    <th>Team Number</th>
                                    <th>Where the Robot Can Score</th>
                                    <th>Where the Robot Can Shoot From (Farthest)</th>
                                    <th>Where the Robot Can Collect From</th>
                                    <th>Can the Robot Climb</th>
                                    <th>Can the Robot Climb Anywhere on the Chain</th>
                                    <th>Can the Robot Climb with Another Robot</th>
                                    <th>Does the Robot Perform Trap</th>
                                    <th>Chassis Type</th>
                                    <th>Drive System Motors</th>
                                    <th>Gear Ratio</th>
                                    <th>Can the Robot Pass Under the Chain</th>
                                    <th>What the Robot Does in Autonomous</th>
                                    <th>Robot Width with Bumpers</th>
                                </tr>
                            </thead>
                            <tbody>
                                {filteredData.map((team, index) => (
                                    <tr key={index}>
                                        <td>{team['Team Number']}</td>
                                        <td>{team['Where the Robot Can Score']}</td>
                                        <td>{team['Where the Robot Can Shoot From (Farthest)']}</td>
                                        <td>{team['Where the Robot Can Collect From']}</td>
                                        <td>{team['Can the Robot Climb']}</td>
                                        <td>{team['Can the Robot Climb Anywhere on the Chain']}</td>
                                        <td>{team['Can the Robot Climb with Another Robot']}</td>
                                        <td>{team['Does the Robot Perform Trap']}</td>
                                        <td>{team['Chassis Type']}</td>
                                        <td>{team['Drive System Motors']}</td>
                                        <td>{team['Gear Ratio']}</td>
                                        <td>{team['Can the Robot Pass Under the Chain']}</td>
                                        <td>{team['What the Robot Does in Autonomous']}</td>
                                        <td>{team['Robot Width with Bumpers']}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                )}
            </div>
        </ThemeProvider>
    );
};

export default PitScouting;
